import {Component, Inject} from '@angular/core';
import {MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {FormControl, FormBuilder, FormGroup, Validators} from "@angular/forms";

@Component({
  selector: 'app-device-dialog',
  templateUrl: './device-dialog.component.html',
  styleUrls: ['./device-dialog.component.css']
})
export class DeviceDialogComponent {

  device: any [];
  myGroup: FormGroup;

  plans = [
    {
      name: 'AT&T Plan #1',
      sku: '92839879871',
      price: '$100.00'
    },
    {
      name: 'AT&T Plan #2',
      sku: '92839879872',
      price: '$100.00'
    },
    {
      name: 'AT&T Plan #3',
      sku: '92839879873',
      price: '$100.00'
    }
  ];

  features = [
    {
      name: 'Feature #1',
      sku: '92839879453',
      price: '$10.00'
    },
    {
      name: 'Feature #2',
      sku: '92839879454',
      price: '$11.00'
    },
    {
      name: 'Feature #3',
      sku: '92839879455',
      price: '$12.00'
    }
  ];

  constructor(public dialogRef: MatDialogRef<DeviceDialogComponent>,
              private _formBuilder: FormBuilder,
              @Inject(MAT_DIALOG_DATA) public data: any) {
              this.device = data;

    this.myGroup = this._formBuilder.group({
      nameCtrl: [data.name, Validators.required],
      skuCtrl: [data.sku, Validators.required],
      priceCtrl: [data.price, Validators.required],
    });
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

}



